// If you need to post to the server, use this module ... or just use fetch().
// USAGE:
//   new Post('http://example.com/cow', {milk:true})
//   .cache() // Do this if you want the Post to auto-cache. If it's already been cached, then the cached version will be returned at this point instead of the newly created instance. This should be called immidietly after instanciation if used.
//   .onData((data) => {console.log('DATA:', data)});
//   .onError(() => {console.log(':(')});
//   .send()

let _cachedPosts = {}
class Post {
  _METHOD = 'POST';
  constructor(url, args) {
    this._url = url;
    this._args = args;

    // Listeners for a response
    this._onData = [];
    this._onError = [];
    this._requestSent = false;

    // Data to be filled after a response
    this._responded = false;
    this._reponseData = null;
    this._errored = false;
  }

  cache() {
    // Caches the Post object then returns itself.
    // If an equivalent Post object has already been cached, then that will be returned instead.
    // This should be called immidietly after instanciation.
    let args={};
    for (let key of Object.keys(this._args).sort()) {
      args[key] = this._args[key]; // Sorts the args so that the string made from them will always be the same.
    };
    let identifier = `${this._url} ${JSON.stringify(args)}`; // A unique identifier used for storing and recalling the cached posts.

    if (!_cachedPosts[identifier]) {
      _cachedPosts[identifier] = this;
      return this;
    } else {
      return _cachedPosts[identifier];
    }
  }

  onData(callback) {
    // callback will recieve the recieved data.
    // If a response has already been recieved, the callback will be triggered immidietly.
    // Returns this.
    if (this._responded) {
      if (!this.errored) callback(this._responseData);
      return this;
    }
    this._onData.push(callback);
    return this;
  }

  onError(callback) {
    // callback will recieve the recieved data (which would probably be the error message).
    // If a response has already been recieved, the callback will be triggered immidietly.
    // Returns this.
    if (this._responded) {
      if (this.errored) callback(this._responseData);
      return this;
    }
    this._onError.push(callback);
    return this;
  }

  _onDataWrapper(data) {
    // The real onData event that calls all the other onData listeners
    for (let f of this._onData) f(data);
    this._responded = true;
    this._responseData = data;
    this._onData = this._onError = [];
  }
  _onErrorWrapper(data) {
    // The real onError event that calls all the other onError listeners
    for (let f of this._onError) f(data);
    this._responded = true;
    this._errored = true;
    this._responseData = data;
    this._onData = this._onError = [];
  }
  send() {
    // Sending the request. If already sent, then nothing happens.
    if (this._requestSent) return;

    let requestData = [];
    for (let key in this._args) {
      requestData.push(`${key}=${this._args[key]}`);
    }
    let dataStr = requestData.join('&');

    let post = new XMLHttpRequest();
    if (this._METHOD==='GET')
      post.open(this._METHOD, this._url+'?'+dataStr, true);
    else {
      post.open(this._METHOD, this._url, true);
      post.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    }

    post.onreadystatechange = () => {
      if (post.readyState === XMLHttpRequest.DONE) {
        if (post.status>=300) {
          this._onErrorWrapper(post.responseText);
        } else {
          this._onDataWrapper(post.responseText);
        }
      }
    }

    post.onerror = () => this._onErrorWrapper();
    if (this._METHOD==='GET')
      post.send();
    else
      post.send(dataStr);
    this._requestSent = true;
  }
}

class Get extends Post {
  _METHOD = 'GET';
}

export {Post, Get}
