// Higher order compontent in charge of fetching data.
import React from 'react';
import UptimeTile from './UptimeTile';

class Uptime extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
  };
  dayRange = null;

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady(days) {
    this.dayRange = days;
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/uptime', {
      days: days,
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }
  onDataReady(data) {
    let {uptime, start} = JSON.parse(data);
    if (uptime.toString().includes('.')) {
      uptime = uptime.toFixed(2);
    }

    let lastDateAvailable = new Date(start);
    let outOfRange = new Date();
    outOfRange.setDate(outOfRange.getDate() - this.dayRange); // This finds the minimum acceptable date to use within the daterange.
    if (lastDateAvailable<outOfRange) lastDateAvailable = undefined;

    this.setState({
      loaded: true,
      data: uptime,
      lastDateAvailable: lastDateAvailable,
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    });
  }

  render() {
    return (
      <UptimeTile data={this.state.data} lastDateAvailable={this.state.lastDateAvailable}
             loaded={this.state.loaded} loadFailed={this.state.loadFailed}
             util={this.props.util}/>
    )
  }
}

export default Uptime;
