// Higher order compontent in charge of fetching data.
import React from 'react';
import PageSpeedTile from './PageSpeedTile';

class PageSpeed extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
  };

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady() {
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/report/get', {
      location: 'pagespeed',
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }
  onDataReady(data) {
    // We grab the most recent page speed data
    data = JSON.parse(data);
    let latestTimestamp = 0;
    for (let row of data) {
      let row_timestamp = parseInt(row.timestamp,10);
      if (row_timestamp > latestTimestamp) {
        latestTimestamp = row_timestamp;
      }
    }
    // We want to find the average of multiple test results (It's running multiple tests at once for this purpouse)
    let pageSpeedSum = 0
    let pageSpeedCount = 0
    for (let row of data) {

      let loadTime, isCached;
      if ('loadTime' in row) { // If this row in the csv contains the newer csv format.
        loadTime = row.loadTime;
        isCached = row.cached;
      } else { // Otherwise the row contains csv from before the pagespeed server updated its csv format.
        loadTime = row['Load Time (ms)'];
        isCached = row.Cached;
      }

      if (parseInt(row.timestamp,10) === latestTimestamp && isCached==='1') { // We're only grabbing the results done from a cached load for now.
        pageSpeedSum += parseInt(loadTime,10)/1000; // The data gets stored in milleseconds, we'll convert it to seconds.
        pageSpeedCount++;
      }
    }
    this.setState({
      loaded: true,
      data: (pageSpeedSum/pageSpeedCount).toFixed(2),
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    });
  }

  render() {
    return <PageSpeedTile data={this.state.data} loaded={this.state.loaded} loadFailed={this.state.loadFailed}/>
  }
}

export default PageSpeed;
