// Higher order compontent in charge of fetching data.
import React from 'react';
import NetGainTile from './NetGainTile';

class NetGain extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
    dataXMap: undefined,
    data_coverage: undefined,
  };

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady(days) {
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
      dataXMap: undefined,
      data_coverage: undefined,
    });
    this.props.util.reportingPost('/woocommerce/sales', {
      days: days,
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .cache()
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send()
  }
  onDataReady(data) {
    data = JSON.parse(data);
    let wanted_data = []
    let dataXMap = {};
    for (let i=0; i<data.length; ++i) {
      const day = data[i];
      dataXMap[i] = day['timestamp'];
      wanted_data.push([i, Math.round(day['profit'])]);
    }
    this.setState({
      loaded: true,
      data: [{
        "key" : "NetGain",
        "color": "#262",
        "values" : wanted_data
      }],
      dataXMap,
      data_coverage: data[0].covers,
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    });
  }

  render() {
    return (
      <NetGainTile data={this.state.data} data_coverage={this.state.data_coverage}
                   dataXMap={this.state.dataXMap}
                   loaded={this.state.loaded} loadFailed={this.state.loadFailed}
                   util={this.props.util}/>
    );
  }
}

export default NetGain;
