import React from 'react';
import './AnalyticsWalkthroughBackground.css';

import d3 from 'd3';
import nv from 'nvd3';
import 'nvd3/build/nv.d3.min.css';

let data = [{ // False data to render the background. (Remember the graphs stack on eachother, they aren't independent graphs.)
  'values': [ [1, 6], [2 , 5], [3, 3], [4, 4], [5, 5],
              [6, 5], [7, 7], [8, 5], [9, 6], [10, 5],
              [11, 6], [12, 5], [13, 7], [14, 7], [15, 6],
              [16, 5], [17, 4], [18, 3], [19, 5], [20, 7],
              [21, 5], [22, 5], [23, 6], [24, 4], [25, 4] ]
},{
  'values': [ [1, 2], [2, 4], [3, 4], [4, 6], [5, 9],
              [6, 8], [7, 8], [8, 10], [9, 11], [10, 11],
              [11, 11], [12, 12], [13, 11], [14, 2], [15, 4],
              [16, 4], [17, 7], [18, 7], [19, 8], [20, 10],
              [21, 9], [22, 10], [23, 8], [24, 8], [25, 9] ]
}]

class AnalyticWalkthroughBackground extends React.Component {

  constructor() {
    super();
    this.chart = undefined;
    nv.utils.windowResize(() => {
      if (this.chart) {
        this.chart.update()
      }
    });
  }

  render() {
    nv.addGraph(() => {
      this.chart = nv.models.stackedAreaChart()
      .margin({left: 0, right: 0, top: 0, bottom: 0})
      .x((d) => { return d[0] })
      .y((d) => { return d[1] })
      .showControls(false)
      .showLegend(false)
      .color(['rgb(110,0,0)','rgb(125,0,0)'])
      .duration(0)
      ;

      d3.select('.analytic-walkthrough-background svg')
      .datum(data)
      .call(this.chart);

      return this.chart;
    });

    return (
      <div className='page-background analytic-walkthrough-background'>
        <svg/>
      </div>
    );
  }

}

export default AnalyticWalkthroughBackground;
