import React from 'react';
import './UptimeTile.css';

class UptimeTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-update'/>
  }
  render_data() {
    let lastDateAvailable = this.props.lastDateAvailable;
    let OutOfDateRangeEl = this.props.util.OutOfDateRangeEl;

    return (
      <div className='widget widget-uptime'>
        <h2 className='widget-uptime-label'>Uptime</h2>
        <h1 className='widget-uptime-value'>{this.props.data}%</h1>
        <div className='widget-uptime-background'></div>
        {lastDateAvailable && (<OutOfDateRangeEl className='widget-uptime-info-icon' lastDay={lastDateAvailable}/>)}
      </div>
    );
  }
  render_error() {
    return (
      <div className='widget widget-uptime'>
        <span className='widget-uptime-error'>Could not load the uptime :(</span>
      </div>
    );
  }
}

export default UptimeTile;
