import React from 'react';
import d3 from 'd3';
import nv from 'nvd3';
import 'nvd3/build/nv.d3.min.css';

import AnalyticsTileUnauthed from './AnalyticsTileUnauthed';
import './AnalyticsTile.css';

class AnalyticsTile extends React.Component {
  constructor() {
    super();
    this.chart = undefined;
    nv.utils.windowResize(() => {
      if (this.chart) {
        this.chart.update()
      }
    });
  }

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else if (!this.props.authed) {
      return this.render_unauthed();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-analytics widget-analytics-background'/>
  }
  render_data() {
    nv.addGraph(() => {
      this.chart = nv.models.stackedAreaChart()
      .margin({left: 0, right: 0, top: 0, bottom: 0})
      .x((d) => { return d[0] })
      .y((d) => { return d[1] })
      .useInteractiveGuideline(true)
      .showControls(false)
      .showLegend(false)
      .duration(1000)
      ;

      this.chart.interactiveLayer.tooltip.contentGenerator(
        this.props.util.createTooltipWithDate('Sessions', {
          yValueIsDollar:false, color:'#500', xMap:this.props.dataXMap
        })
      );

      d3.select('.widget-analytics svg')
      .datum(this.props.data)
      .call(this.chart);

      return this.chart;
    });

    let total = this.props.data[0].values.reduce((total, item) => {return total+item[1]}, 0);

    return (
      <a className='widget widget-analytics widget-analytics-background'
      href='https://analytics.google.com/analytics/web/' target='_blank' rel='noopener noreferrer'>
        <h2 className='widget-analytics-header'>Website Sessions</h2>
        <svg/>
        <h3 className='widget-analytics-footer'>Total Sessions: {total}</h3>
      </a>
    );
  }
  render_unauthed() {
    const {changePage} = this.props;

    return (
      <div className='widget widget-analytics'>
        <AnalyticsTileUnauthed changePage={changePage} util={this.props.util}/>
      </div>
    );
  }
  render_error() {
    return (
      <div className='widget widget-analytics widget-analytics-background'>
        <span className='widget-analytics-error'>Could not load the analytics :(</span>
      </div>
    );
  }
}

export default AnalyticsTile;
