// Higher order compontent in charge of fetching data.
import React from 'react';
import ActivityTile from './ActivityTile';

class Activity extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
  };

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady() {
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/report/get', {
      location: 'site-activity',
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send()
  }
  onDataReady(rawData) {
    rawData = JSON.parse(rawData);
    let data = [];
    for (let i=rawData.length-1; i>=0; --i) { // Iterate backwards so the items appear newest to oldest.
      data.push({date: new Date(parseInt(rawData[i].timestamp,10)), message: rawData[i].activity});
    }
    this.setState({
      loaded: true,
      data: data,
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    });
  }

  render() {
    return <ActivityTile data={this.state.data} loaded={this.state.loaded} loadFailed={this.state.loadFailed}/>
  }
}

export default Activity;
