import React from 'react';
import './LockoutTile.css';

class LockoutTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-lockout'/>
  }
  render_data() {
    let data = this.props.data > 99999 ? '99999+' : this.props.data.toString();
    let sizeClass = `digits-${data.length}`;

    let {lastDateAvailable} = this.props;
    let {OutOfDateRangeEl, InfoBubbleEl} = this.props.util;

    return (
      <span className='widget widget-lockout'>
        <a target='_blank' rel='noopener noreferrer'
        href='/wp-admin/index.php?page=itsec-dashboard'>
          <h1 className={'widget-lockout-value '+sizeClass}>{data}</h1>
          <h2 className={'widget-lockout-label '+sizeClass}>Bot<br/>Break-in<br/>Failures</h2>
        </a>
        <InfoBubbleEl className='widget-lockout-info-icon'
        message='Total failed authentication attempts. A large portion of these can come from bots trying to break into your site.'/>
        {lastDateAvailable && (<OutOfDateRangeEl className='widget-lockout-info-icon' lastDay={lastDateAvailable}/>)}
      </span>
    );
  }
  render_error() {
    return (<div className='widget widget-lockout'><span className='widget-lockout-error'>Could not load the lockouts :(</span></div>)
  }
}

export default LockoutTile;
