import React from 'react';
import './AnalyticsWalkthrough.css';

import step1 from './step1.jpg'
import step2 from './step2.jpg'
import step3 from './step3.jpg'
import smallLoader from './small-loader.gif'

let step3_alt_text = 'Place "Russell@wordpressoverwatch.com" in the "Add Permission for" field. '+
'Check the "Notify this user by email" box. In the Read & Analyze menu check "Manage Users" '+
'and if wanted check "Edit". Click the "Add" button.'

let form_loading_style = {
  backgroundImage: `url(${smallLoader})`,
  backgroundPosition: 'center',
  backgroundSize: '28px 28px',
  backgroundRepeat: 'no-repeat',
  width: 64,
  height: 42,
}
let form_button_normal = <button>Contact!</button>
let form_button_waiting = <button style={form_loading_style} disabled></button>
let form_button_submitted = <button style={{width:64}} disabled><span role='img' aria-label='Submitted'>&#9989;</span></button>

class AnalyticWalkthrough extends React.Component {

  constructor() {
    super();
    this.state = {
      canSubmit: true,
      hasSubmitted: false,
      isQExpanded: false,
    }
    this._submit_text = null;
  }

  render() {
    return (
      <div className='page analytic-walkthrough'>
        <h1 className='walkthrough-close' onClick={() => this.props.leave()}>X</h1>
        <h1 className='walkthrough-header'>Grant Access to Google Analytics</h1>
        <h2 className='walkthrough-step'>Sign in to your Google account <a href='https://analytics.google.com/analytics/web/#management/Settings/'>here</a></h2>
        <div className='walkthrough-img-container walkthrough-img-container-center'> <img className='walkthrough-img walkthrough-img-center' src={step1} alt=''/> <div className='walkthrough-imgShadow'/></div>
        <h2 className='walkthrough-step'>Click "User Management"</h2>
        <div className='walkthrough-img-container'> <img className='walkthrough-img' src={step2} alt=''/> <div className='walkthrough-imgShadow'/> </div>
        <h2 className='walkthrough-step'>Fill in some info</h2>
        <div className='walkthrough-img-container'> <img className='walkthrough-img'  alt={step3_alt_text} src={step3}/> <div className='walkthrough-imgShadow'/> </div>
        <h2 className='walkthrough-step'>And you're done!</h2>
        <h3 className='walkthrough-stepinfo'>Please give us a business day to put your analytic data on your dashboard</h3>
        <button className='walkthrough-closebottom' onClick={() => this.props.leave()}>Return to dashboard</button>
        {this.render_footer()}
      </div>
    )
  }

  onContact(e) {
    e.preventDefault();
    const util = this.props.util
    const {apiUsername, apiKey, wpEmail, wpUserId} = util.credentials

    let message = this._submit_text.value
    if (message.trim() === '') return;

    message = util.dashboardTarget+' wants your help. This message was sent from the Analytic Setup page on their Site Health Dashboard\'s\n\n' + message
    message = util.escapeHTML(message).replace('\n','<br/>')

    this.setState({canSubmit: false});

    fetch(util.services['Tickets']+'/v1/ticket', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': 'Basic ' + btoa(btoa(apiUsername)+':'+apiKey),
      },
      body: new URLSearchParams({
        title: util.dashboardTarget + ' wants help!',
        message,
        category: 'website configuration',
        source: 'OW_PLUGIN:DASHBOARD',
        domain: util.dashboardTarget,
        email: wpEmail,
        wpUserId,
      }).toString(),
    })
    .then(r => r.ok ? this.onSubmitDone() : this.onSubmitFail())
    .catch(() => this.onSubmitFail())
  }
  onSubmitDone() {
    this._submit_text.value = ''
    this.setState({
      'hasSubmitted': true
    })

    window.setTimeout(() => {
      this.setState({
        'canSubmit': true,
        'hasSubmitted': false
      })
    }, 3000)
  }
  onSubmitFail(e) {
    alert('Submission failed :(. Try emailing us instead.')
    this.setState({
      'canSubmit': true,
      'hasSubmitted': false
    })
  }

  render_footer() {
    return (
      <footer>
        { /* Legacy config files don't have a way of getting this value, so we'll disable the comment form in that case */
          !this.props.util.services['Tickets']
          ? <div style={{height:'250px'}}></div>
          : (
            <form id='walkthrough-contact' className='walkthrough-contact' action='' onSubmit={(e) => this.onContact(e)}>
              <h2>Still need help?</h2>
              <textarea name='message' form='walkthrough-contact' placeholder='Email us!'
                        disabled={!this.state.canSubmit} ref={el => this._submit_text = el}/>
              <br/>
              <div className='walkthrough-submit-area'>
                <p>
                  Replies will be sent to<br/>
                  {this.props.util.credentials.wpEmail}<br/>
                  <a href='/wp-admin/profile.php#email'>Change?</a>
                </p>
                {this.state.canSubmit?(
                  form_button_normal
                ):(
                  this.state.hasSubmitted?form_button_submitted:form_button_waiting
                )}
              </div>
            </form>
          )
        }
        <div className='walkthrough-expand'>
          <p className='walkthrough-expand-title' onClick={() => this.expandQ()}>Why do we want access to your account?</p>
          <p className='walkthrough-expand-content' style={{height: this.state.isQExpanded?120:0}}>
              When you give us access to your analytics account it will let us show analytic data on your dashboard.
              As an added bonus if you ever need help with your analytics we will be able to lend a hand easier
              since we are now able to view it ourselves. <br/><br/>
              See our <a href='https://wp-overwatch.com/legal/software-privacy-policy.html'>Privacy Policy</a>
          </p>
        </div>
      </footer>
    );
  }
  expandQ() {
    this.setState({isQExpanded: !this.state.isQExpanded})
  }
}

export default AnalyticWalkthrough;
