import React from 'react';
import ActivityWidget from './widgets/Activity.js';
import AnalyticsWidget from './widgets/Analytics.js';
import LockoutWidget from './widgets/Lockout.js';
import BackupWidget from './widgets/Backup.js';
import NetGainWidget from './widgets/NetGain.js';
import SalesWidget from './widgets/Sales.js';
import UptimeWidget from './widgets/Uptime.js';
import UpdateWidget from './widgets/Update.js';
import PageSpeedWidget from './widgets/PageSpeed.js';
import './Content.css';

class Content extends React.Component {

  render() {
    let util = this.props.util;
    let config = this.props.util.getConfig();

    return (
      <div className={'content '+(this.props.hide?'hidden-content':'shown-content')}>
        {config.activityFeed && <ActivityWidget util={util}/>}

        <AnalyticsWidget changePage={this.props.changePage} util={util}/>
        <div className={'security-widgets'+(config.doesSales?'':' nosales')}>
          <LockoutWidget util={util}/>
          <BackupWidget util={util}/>
        </div>
        {config.doesSales && <NetGainWidget util={util}/>}
        {config.doesSales && <SalesWidget util={util}/>}

        <div className='evensize-widget-container'>
          <UptimeWidget util={util}/>
        </div>
        <div className='evensize-widget-container'>
          <UpdateWidget util={util}/>
        </div>
        <div className='evensize-widget-container'>
          <PageSpeedWidget util={util}/>
        </div>

      </div>
    );
  }
}

export default Content;
