// Higher order compontent in charge of fetching data.
import React from 'react';
import UpdateTile from './UpdateTile';

class Update extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
    lastDateAvailable: undefined, // Stores when the first occurance of data was. This is undefined when it's outside the dayRange.
  };
  dayRange = null;

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady(days) {
    this.dayRange = days;
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/report/get', {
      location: 'updates',
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }

  onDataReady(data) {
    data = JSON.parse(data);

    let outOfRange = new Date();
    outOfRange.setDate(outOfRange.getDate() - this.dayRange); // This finds the minimum acceptable date to use within the daterange.

    let lastDateAvailable = new Date(); // Stores the first occurance of something in the data.
    let total = 0;
    for (let row of data) {
      let rowsDate = new Date(parseInt(row.timestamp,10));
      if (lastDateAvailable>rowsDate) lastDateAvailable = rowsDate;
      if (outOfRange<rowsDate) { // We remove anything out of the date range.
        total+=1;
      }
    }
    if (lastDateAvailable<outOfRange) lastDateAvailable=undefined;

    this.setState({
      loaded: true,
      data: total,
      lastDateAvailable: lastDateAvailable,
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    })
  }

  render() {
    return <UpdateTile data={this.state.data} lastDateAvailable={this.state.lastDateAvailable}
            loaded={this.state.loaded} loadFailed={this.state.loadFailed}
            util={this.props.util}/>
  }
}

export default Update
