// Higher order compontent in charge of fetching data.
import React from 'react';
import LockoutTile from './LockoutTile';

class Lockout extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
    lastDateAvailable: undefined, // Stores when the first occurance of data was. This is undefined when it's outside the dayRange.
  };
  dayRange = null;

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady(days) {
    this.dayRange = days;
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/security/get', {
      target: this.props.util.dashboardTarget,
      days: this.dayRange,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }
  onDataReady(data) {
    data = JSON.parse(data);
    this.setState({
      loaded: true,
      data: (data['local-brute-force']||0) + (data['network-brute-force']||0),
      lastDateAvailable: data.lastDateAvailable && new Date(data.lastDateAvailable),
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    })
  }

  render() {
    return <LockoutTile data={this.state.data} lastDateAvailable={this.state.lastDateAvailable}
            loaded={this.state.loaded} loadFailed={this.state.loadFailed} util={this.props.util}/>
  }
}

export default Lockout;
