import React from 'react';
import Header from './Header.js';
import Content from './Content.js';
import Footer from './Footer.js';
import './AppTile.css';

class AppTile extends React.Component {
  backgroundColor = null; // A background color that goes ontop of the default gray color. The footer uses this when changing it's color.
  appContainerEl = null;

  render() {
    const onScroll = (...args) => this.onScroll(...args);
    const addScrollListener = (...args) => this.addScrollListener(...args);
    const appContainerStyle = {
      backgroundColor: this.backgroundColor,
      visibility: (this.props.firstLoad&&this.props.background)?'hidden':'visible' // If you're loading a sub-page directly (from the url-bar), you don't want to see the root content flash in than out, so we hide it.
    }
    const appContainerRef = (appContainer) => this.appContainerEl = appContainer;
    const {background, changePage, page} = this.props

    return (
      <div> {/*This container holds any pages that needs to be rendered.*/}
        {background}
        <div className='app-container'
             style={appContainerStyle}
             onScroll={onScroll}
             ref={appContainerRef}> {/*This container is in charge of making a scrollbar for the app.*/}
          <div className='app'> {/*This renderes the actual dashboard*/}
            <Header hide={!!background} dateRangeChoices={this.props.dateRangeChoices} changeDateRange={this.props.changeDateRange}/>
            <Content hide={!!background} changePage={changePage} util={this.props.util}/>
            <Footer hide={!!background} addScrollListener={addScrollListener}/>
          </div>
        </div>
        {page}
      </div>
    )
  }

  scrollListeners = []
  onScroll() {
    let utilities = {setBackgroundColor: (...args) => this.setBackgroundColor(...args)}
    for (let listener of this.scrollListeners) {
      listener(utilities);
    }
  }

  addScrollListener(listener) {
    // Listens for scroll events. The listener is called with an object containing useful functions.
    // Returns a function to remove the listener.
    let removeListener = () => {
      for (let i=0; i<this.scrollListeners.length; ++i) {
        if (this.scrollListeners[i] === listener) {
          this.scrollListeners.splice(i,1);
          return;
        }
      }
    }
    this.scrollListeners.push(listener);
    return removeListener;
  }

  setBackgroundColor(color) {
    this.backgroundColor = color;
    this.appContainerEl.style.backgroundColor = color; // Instead of calling the render function we apply the style directly for performance reasons.
  }
}

export default AppTile;
