import React from 'react';
import AppTile from './AppTile';
import ErrorPage from './pages/ErrorPage';
import ErrorPageBackground from './pages/ErrorPageBackground';
import AnalyticWalkthrough from './pages/AnalyticWalkthrough';
import AnalyticWalkthroughBackground from './pages/AnalyticWalkthroughBackground';

class App extends React.Component {
  state = {
    background: null, // The element of a background for a page. The page and it's background are seperated for cleaner animations (e.g. the page's background can fade in while the dashboard is fading out and before the actual page fades in.)
    page: null, // The element for the page. (Sometimes background or page is null if there isn't a page to display)
  }
  dateRangeChoices = ['Past Month', 'Past Year'] // Different functions assume the default value of this list is the first item.
  loadingPageTimeout = null;
  firstLoad = true; // This is set to false after the first render.

  constructor() {
    super();
    // What happens if the state is popped before the dom is ready? Is that possible?
    window.addEventListener('popstate', () => {
      this._updatePage()
    });
  }

  componentWillMount() {
    this._updatePage() // If we're initially loading a sub-page, get ready to load it.
  }
  componentDidMount() {
    this.changeDateRange(this.dateRangeChoices[0]); // Gives it it's default value.
    // Webpack seems to have problem loading local fonts, so we're loading it
    // here after the fact. We also need to give it an absolute url just in case
    // the user initially loaded a sub-page.
    let style = document.createElement('style');
    let fontUrl = this.props.util.getConfig().base_url+'haiku-monkey-insolent.otf'
    style.innerHTML = `@font-face {font-family: haiku-monkey; src: url(${fontUrl}); }`
    document.head.appendChild(style)
  }

  render() {
    let changePage = (...args) => this.changePage(...args);
    let changeDateRange = (...args) => this.changeDateRange(...args);
    let firstLoad = this.firstLoad;
    this.firstLoad = false;

    return (<AppTile dateRangeChoices={this.dateRangeChoices} changeDateRange={changeDateRange}
                     background={this.state.background} page={this.state.page} firstLoad={firstLoad}
                     changePage={changePage} util={this.props.util}/>)
  }

  changeDateRange(option) {
    let days;
    switch (option) {
      case this.dateRangeChoices[0]: // Past Month
        days = 30;
        break;
      case this.dateRangeChoices[1]: // Past Year
        days = 365;
        break;
      default: throw new Error('Invalid option: '+option.toString())
    }
    this.props.util.setDateRange(days);
  }

  changePage(pageName='') {
    let url;
    if (pageName) {
      url = `${this.props.util.getConfig().base_url}${pageName}/${window.location.search}`;
    } else {
      url = this.props.util.getConfig().base_url+window.location.search;
    }
    window.history.pushState(null, pageName, url);
    this._updatePage();
  }
  _updatePage() {
    // Same a changePage but without changing history, and it finds the page to load
    // based off the url.

    if (this.loadingPageTimeout) window.clearTimeout(this.loadingPageTimeout);
    // Figures out what page we're on.
    let baseUrl = this.props.util.getConfig().base_url;
    let currentUrl = window.location.pathname;
    if (currentUrl[currentUrl.length-1] !== '/') currentUrl += '/'; // We add the slash because baseUrl has one. This prevents errors if the root url is being loaded.
    let pageName = currentUrl.slice(baseUrl.length, -1) // -1 removes the slash

    let background, page;
    const leavePage = () => this.changePage();
    if (pageName === '') {
      this.setState({page: null, background: null})
      return;
    } else if (pageName === 'analytic-walkthrough') {
      background = <AnalyticWalkthroughBackground/>
      page = <AnalyticWalkthrough leave={leavePage} util={this.props.util}/>
    } else {
      background = <ErrorPageBackground/>
      page = <ErrorPage leave={leavePage} message="Page not found."/>
    }

    this.setState({background: background})
    this.loadingPageTimeout = window.setTimeout(() => {
      this.setState({page: page})
    }, 300)
  }
}

export default App;
