import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Util from './util/util';
import {Post, Get} from './util/request';
import 'core-js/shim'; // Polyfill for javascript
import './index.css';

const config = window.config;

// Goes to https if not already there.

if (config.force_ssl && window.location.protocol !== 'https:') {
  // This actually can fix problems if wordpress itself forces https, because when trying
  // to check if the user is logged in through http when wordpress forces https,
  // wordpress will simply say the user is not logged in
  window.location.href = 'https:' + window.location.href.substring(window.location.protocol.length);
}

// Let's make sure some aspects of config.js are done right

if (!config.auth_url.endsWith('/') || !config.wp_dir.endsWith('/') || !config.base_url.endsWith('/')) {
  document.write('The config file isn\'t set up correctly');
  console.warn('urls in config.js should always end with /. We\'re picky about our urls.');
}
if (config.wp_dir.includes('://') || config.wp_dir.startsWith('/')) {
  document.write('The config file isn\'t set up correctly');
  console.warn('wp_dir must be a relative url');
}

// Handle legacy config files.
if (config.login_token && !config.dev_api_key) {
  config.dev_api_key = config.login_token
  config.bypass_login = true
  console.warn('WARNING: A config file was provided with the deprecated key "login_token". Please use dev_api_username/dev_api_key instead.')
}

// Fetch services from service registry

let services = null
{
  const {service_registry, service_registry_env, dev_api_username, dev_api_key, reporting_server} = config
  if (!service_registry && reporting_server) { // Legacy config files don't provide this field.
    let index = reporting_server.lastIndexOf('/v2')
    console.warn('WARNING: A config file was provided with the deprecated key "reporting_server". Please use "service_registry" instead.')
    if (index===-1) {
      document.write('The config file isn\'t set up correctly');
      console.warn('reporting_server needs to end with /v2')
    }
    services = {'Reporting': reporting_server.slice(0, index)}
    fetchComplete()
  } else {
    let request
    if (service_registry_env) {
      request = new Post(service_registry+'/v1/services.php', {
        username:dev_api_username, api_key:dev_api_key, env:service_registry_env
      })
    } else {
      request = new Get(service_registry+'/v1/services.json')
    }
    request
    .onData(services_ => {
      services = JSON.parse(services_)
      fetchComplete()
    })
    .onError(onServicesFetchError)
    .send();
  }

  function onServicesFetchError(message) {
    showErrorScreen('I was unable to communicate with the server.', message)
  }
}

// Before we start showing the dashboard, let's check to see if they're logged in.

let credentials = null
{
  const {dev_api_username, dev_api_key, bypass_login, auth_url} = config
  if (bypass_login) {
    // For development purpouses the config.js can contain a development api-key to skip logging in.
    credentials = {apiKey:dev_api_key, apiUsername:dev_api_username}
    fetchComplete()
  } else {
    new Post(auth_url, {format:'json', 'wordpress-dir': '../'+config.wp_dir}) // ../ is added because wp-authenticator is up a directory.
    .onData(data => {
      if (data.startsWith('{'))
        credentials = JSON.parse(data)
      else // The legacy authenticator did not return json, it only returned the api-key.
        credentials = {apiUsername:null, apiKey:data}
      fetchComplete()
    })
    .onError(onAuthError)
    .send()
  }

  function onAuthError(message) {
    // If the error has to do with not being logged in, an automatic redirection will happen.
    if (message === "Error: User is not logged in") {
      window.location.replace(config.wp_dir + `wp-login.php?redirect_to=${encodeURI(window.location.href)}`);
      return;
    }
    showErrorScreen('I was unable to talk to the authenticator.', message)
    console.log('If you are developing right now you probably forgot to start the php part of the server. You can bypass login by setting bypass_login to true in config.js')
  }
}

// Two REST requests need to be made before we show the application.
// This function is called with either of them complete. Inside we check that we have data
// from both requests.
let isShowingErrorScreen = false
function fetchComplete() {
  if (services && credentials && !isShowingErrorScreen)
    show(services, credentials)
}
function show(services, credentials) {
  let util = new Util(config, services, credentials);
  ReactDOM.render(
    <App util={util}/>,
    document.getElementById('root')
  );
}

function showErrorScreen(display_message, raw_error) {
  // display_message is what's shown to the user.
  // raw_error get's put in console.warn().
  if (!isShowingErrorScreen) {
    isShowingErrorScreen = true
    ReactDOM.render(
      <div className='error'>
        <h1>An error occured :(</h1>
        <p>{display_message}</p>
        <p>This wasn't supposed to happen, Please contact WordpressOverwatch and tell them to fix it.</p>
      </div>,
      document.getElementById('root')
    )
  }
  console.warn('ERROR:',raw_error)
}
