import React from 'react';
import './ActivityTile.css';

class ActivityTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-activity'/>
  }
  render_data() {
    function renderMessage(messageData, i) {
      let dateStr = messageData.date.toLocaleString('en-us', {month: 'short', day: 'numeric'});
      return (
        <div key={i} className='widget-activity-message' style={{animationDelay: (i/6)+'s'}}>
          <span className='date'>{dateStr}</span><span className='content' dangerouslySetInnerHTML={{__html: messageData.message}}/>
        </div>
      )
    }
    return (
      <div className='widget widget-activity'>
        <div className='widget-activity-container'>
          {this.props.data.map(renderMessage)}
        </div>
        <div className='widget-activity-header'>Recent site changes</div>
      </div>
    );
  }
  render_error() {
    return (<div className='widget widget-activity'><span className='widget-activity-error'>Could not load the activity feed :(</span></div>)
  }

}

export default ActivityTile;
