import React from 'react';
import './Header.css';

class Header extends React.Component {
  state = {
    opened: false,
    selected: 0,
  }
  menuEl = null;
  skipNextOpen = false; // Workaround for sticky situation.

  openMenu() {
    if (this.skipNextOpen) {
      this.skipNextOpen = false;
      return;
    }
    this.setState({opened: true})
  }
  fixClickthrough_mousedown() {
    // If the menu is opened and the menu-opener button is clicked, then without
    // this function intervining, the menu will close due to the blur, then re-open
    // immidietly after.
    if (this.state.opened) {
      this.skipNextOpen = true;
    }
  }
  fixClickthrough_mouseout() {
    // Makes it so if the menu is open, and the user pressed on the opener button,
    // moves the mouse out, and lets go, the effect of fixClickthrough_mousedown()
    // gets canceled. Without this functionality the program will get fooled, and the
    // user will have to click the button twice the next time to see it apear.
    this.skipNextOpen = false;
  }
  closeMenu(selected=null) {
    if (selected === this.state.selected || selected === null) {
      this.setState({opened: false});
      return;
    }
    this.setState({opened: false, selected: selected});
    this.props.changeDateRange(this.props.dateRangeChoices[selected])
  }

  render() {
    let openMenu = () => this.openMenu();
    let closeMenu = () => this.closeMenu();
    let fixClickthrough_mousedown = () => this.fixClickthrough_mousedown();
    let fixClickthrough_mouseout = () => this.fixClickthrough_mouseout();

    return (
      <div className='header'>
        <div className='header-date-pick'>
          <h1 className={'date-pick-active'+(this.state.opened?' opened':'')} style={{opacity:this.props.hide?0:1}}
              onClick={openMenu} onMouseDown={fixClickthrough_mousedown} onMouseOut={fixClickthrough_mouseout}>
            {this.props.dateRangeChoices[this.state.selected]} <span>▼</span>
          </h1>
          <div className='date-pick-choices' style={{visibility:this.state.opened?'visible':'hidden'}}
               onBlur={closeMenu} tabIndex='-1' ref={(el) => this.menuEl = el}>
            {this.props.dateRangeChoices.map((choice, i) => {
              let closeMenu = () => this.closeMenu(i);
              return <h1 key={i} className='date-pick-choice' style={{opacity:this.props.hide?0:1}} onClick={closeMenu}>{choice}</h1>;
            })}
          </div>
        </div>
      </div>
    );
  }
  componentDidUpdate() {
    if (this.state.opened) { // Implements an autofocus for the menu if it's opened. We must wait until after it's rendered to focus it.
      this.menuEl.focus()
    }
  }
}

export default Header;
