import React from 'react';
import './AnalyticsTileUnauthed.css';

class AnalyticsTile extends React.Component {
  state = {
    flipped: false,
    setupAnalyticsSubmittion: null // Can either be 'success', 'fail', 'submitting', or null depending on how sumbition succeeded when clicking "Set on up!"
  }

  render() {
    return (
      <div className='widget-analytics-unauthed'>
        {this.render_front()}
        {this.render_back()}
      </div>
    );
  }
  render_front() {
    const flip = (...args) => this.flip(...args)

    return (
    <div className={'widget-analytics-background unauth-front'+(this.state.flipped?' flipped':'')}>
      <h2 className='analytic-title'>Want to see Google&nbsp;Analytics&nbsp;here?</h2>
      <div className='unauth-buttonContainer'>
        <span className='unauth-buttonLeft'/>
        <button className='unauth-button' onClick={() => {this.props.changePage('analytic-walkthrough')}}>Grant Access</button>
        <span className='unauth-buttonRight'/>
      </div>
      {!this.props.util.services['Tickets'] ? null : /* Legacy config files don't have a way of getting this value, so we'll disable the comment form in that case */
        <p className='analytic-footer'>Don't have a google analytics? <span className='setup-analytics-link' onClick={flip}>Set one up!</span></p>
      }
    </div>);
  }
  render_back() {
    const flip = (...args) => this.flip(...args);
    const setupAnalytics = (...args) => this.setupAnalytics(...args);

    if (!this.state.setupAnalyticsSubmittion) {
      return (
      <div className={'widget-analytics-background unauth-back'+(this.state.flipped?' flipped':'')}>
        <h1 className='unauth-flip' onClick={flip}>X</h1>
        <h2 className='analytic-back-title'>Just say the word and we'll set Google Analytics up for you.</h2>
        <div className='unauth-back-buttons'>
          <button className='unauth-back-button' onClick={flip}>Never mind</button>
          <button className='unauth-back-button' onClick={setupAnalytics}>Set one up!</button>
        </div>
        <p className='unauth-back-sidenote'>
          We will communicate with you at {this.props.util.credentials.wpEmail}.
          &nbsp;<a href='/wp-admin/profile.php#email'>Change?</a>
        </p>
      </div>);
    } else if (this.state.setupAnalyticsSubmittion === 'submitting') {
      return (
        <div className={'widget-analytics-background unauth-back'+(this.state.flipped?' flipped':'')}>
          <h2 className='analytic-back-title'>Notifying...</h2>
        </div>
      );
    } else if (this.state.setupAnalyticsSubmittion === 'success') {
      return (
        <div className={'widget-analytics-background unauth-back'+(this.state.flipped?' flipped':'')}>
          <h1 className='unauth-flip' onClick={flip}>X</h1>
          <h2 className='analytic-back-title'>Alright, we'll get your analytics set up soon.</h2>
          <div className='unauth-back-buttons'>
            <button className='unauth-back-button' onClick={flip}>Great!</button>
          </div>
        </div>
      );
    } else if (this.state.setupAnalyticsSubmittion === 'fail') {
      return (
        <div className={'widget-analytics-background unauth-back'+(this.state.flipped?' flipped':'')}>
          <h1 className='unauth-flip' onClick={flip}>X</h1>
          <h2 className='analytic-back-title'>Something went wrong :(. Try shooting us an e-mail instead.</h2>
          <div className='unauth-back-buttons'>
            <button className='unauth-back-button' onClick={flip}>Fine</button>
          </div>
        </div>
      );
    } else throw Error();
  }
  render_error() {
    return (<div className='widget widget-analytics'><span className='widget-analytics-error'>Could not load the analytics :(<br/><br/>Maybe reload the page?</span></div>)
  }

  flip() {
    this.setState({flipped: !this.state.flipped})
  }
  setupAnalytics() {
    const util = this.props.util
    const {apiUsername, apiKey, wpEmail, wpUserId} = util.credentials
    this.setState({setupAnalyticsSubmittion: 'submitting'})

    let message = util.dashboardTarget+' wants your help. This message is auto-generated because this user clicked a button saying that they want an analytic account'
    message = util.escapeHTML(message)

    fetch(util.services['Tickets']+'/v1/ticket', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': 'Basic ' + btoa(btoa(apiUsername)+':'+apiKey),
      },
      body: new URLSearchParams({
        title: util.dashboardTarget + ' wants help!',
        message,
        category: 'website configuration',
        source: 'OW_PLUGIN:DASHBOARD',
        domain: util.dashboardTarget,
        email: wpEmail,
        wpUserId,
      }).toString(),
    })
    .then(r => r.ok ? this.setupAnalyticsSuccess() : this.setupAnalyticsFailure())
    .catch(() => this.setupAnalyticsFailure())
  }
  setupAnalyticsSuccess() {
    this.setState({setupAnalyticsSubmittion: 'success'});
  }
  setupAnalyticsFailure() {
    this.setState({setupAnalyticsSubmittion: 'fail'});
  }
}

export default AnalyticsTile;
