import React from 'react';
import './PageSpeedTile.css';

class PageSpeedTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-update'/>
  }
  render_data() {
    return (
      <div className='widget widget-pagespeed'>
        <div className='widget-pagespeed-content'>
          <h1 className='widget-pagespeed-value'>{this.props.data} sec</h1>
          <hr/>
          <h2 className='widget-pagespeed-label'>Page Load<br/>Time</h2>
        </div>
      </div>
    );
  }
  render_error() {
    return (
      <div className='widget widget-pagespeed'>
        <span className='widget-pagespeed-error'>Could not load the page speed :(</span>
      </div>
    );
  }
}

export default PageSpeedTile;
