// This module contains simple utilities that other widgets need.
import React from 'react';
import {Post, Get} from './request.js';
import {ReactComponent as WarningIcon} from './warning.svg';
import {ReactComponent as InfoIcon} from './question-circle.svg';
import './util.css';

class Util {
  // Easy access to utility classes for the outside.
  // Its recommended to use the fetch api instead.
  Post = Post;
  Get = Get;

  constructor(config, services, credentials) {
    this._config = config;
    this._services = services
    this._credentials = credentials;

    this._daysAgo = null;
    this._requestListeners = [];

    this._dashboardTarget = config.target_domain || window.location.hostname
  }

  getConfig() {return this._config} // Its recommended to use .config instead
  get config() {return this._config} // Returns the config file's content.
  get services() {return this._services} // Returns services from the service registry
  get credentials() {return this._credentials} // Returns thou-shalt-not-pass
  get dashboardTarget() {return this._dashboardTarget}

  reportingPost(url, args={}, {method='POST'}={}) {
    // Posts to the reporting server. Url should be an absolute path without the domain e.g. /where/to/post.
    // The token argument will automaticly be appended to the args.
    // A string will be given to the callback, remember to convert it to a json when applicable.

    args = {'api-key':this._credentials.apiKey, ...args}

    if (method==='POST')
        return new Post(this._services['Reporting']+'/v2'+url, args);
    else if (method==='GET')
        return new Get(this._services['Reporting']+'/v2'+url, args);
    else
        throw new Error('ERROR: Method not supported: '+method+ ', but it probably wouldn\'t be hard to add support. Some changes to the code and thats all.')
  }

  // Called when enough data is available to make a request (this might be immidietly)
  // Also called if any of that data changes. Right now this means any time the date-range
  // being viewed is changed. The amount of days to view is passed as an argument.
  onRequestReady(f) {
    this._requestListeners.push(f);
    if (this._daysAgo) { // If data's available to make a request now.
      f(this._daysAgo);
    }
  }
  _triggerRequestReady() {
    this._requestListeners.map(f => f(this._daysAgo))
  }
  // Change the amount of days are being viewed. This should be called at some point
  // with an initial value to tell everyone else what that initial value is.
  setDateRange(dayCount) {
    this._daysAgo = dayCount;
    this._triggerRequestReady()
  }

  _getStandardDate(date) { // Returns a date in this format: month/day/yy
    // Gets the last two digits of a year from a date object.
    let n = date.getUTCFullYear() % 100;
    let year = n>9? n.toString() : '0'+n // Adds a leading 0 if needed for the date.
    // Formats it.
    return `${date.getUTCMonth()+1}/${date.getUTCDate()}/${year}`;
  }
  createTooltipWithDate(labelStr, {yValueIsDollar=false, color='#000', asMonth=false, xMap=undefined}={}) {
    // Creates a function used in chart.tooltip.contentGenerator() to make a tooltip
    // where the x-axis is a date. The y-value is shown after the string "labelStr" in the tooltip.
    // if yValueIsDollar is true, the y-value will be shown with the dollar sign.
    // if asMonths is true then each section will be represented by a month and not a day.
    // xMap will map x-values to a timestamp. The x values that mvd3 provides has rounding errors making
    // timestamps unsoutable as x values. Instead small intigers can be used, and be mapped to to their timestamp using xMap.
    return (info) => {
      let rawX;
      if (typeof info.data !== 'undefined') {
        rawX = info.data[0];
      } else if (typeof info.point !== 'undefined') {
        rawX = info.point[0];
      } else {
        rawX = info.value;
      }
      let date = new Date(xMap?xMap[rawX]:rawX)
      let yValue = info.series[0].value
      if (yValueIsDollar) {
        yValue = yValue.toLocaleString(undefined, {style:'currency', currency:'USD', maximumFractionDigits:0, minimumFractionDigits: 0})
      }
      let dateString;
      if (asMonth) {
        dateString = `${date.toLocaleString('en-us', {month: 'short', timeZone: 'UTC'})} ${date.getUTCFullYear()}`;
      } else {
        dateString = this._getStandardDate(date);
      }
      return `<div class='tooltip' style='font-size: 15px; padding: 5px;'>${labelStr}: <span style='font-weight: bold; color: ${color};'>${yValue}</span><div>${dateString}</div></div>`;
    }
  }

  escapeHTML(text) {
    // Converts text like "<script>" to "&lt;script&gt;"
    let el = document.createElement('p')
    el.innerText = text
    return el.innerHTML
  }

  OutOfDateRangeEl = ({lastDay, className=''}) => {
    // Component that creates a warning symble that can be attached to a widget saying that it's out of range.
    // lastDay is the lastDay that could be reached, this will be displayed to the user when they hover this element.
    return this._renderInfoBubble({
      message: `Inaccurate because we\nstarted tracking this ${this._getStandardDate(lastDay)}`,
      // icon: <img src={warningIcon} alt='warn' style={{width:'24px',height:'24px'}}/>,
      icon: <WarningIcon style={{width:'24px',height:'24px'}}/>,
      className, pos: 'bottom right', tooltipSize: ['220px', '40px'],
    });
  }

  InfoBubbleEl = ({message, className=''}) => {
    // Set the color with css using `path {fill: <color>;}``
    return this._renderInfoBubble({
      icon: <InfoIcon style={{width:'32px', height:'32px'}}/>,
      message, className, pos: 'top left', size: '32px',
      tooltipSize: ['220px', '75px'], rightTooltipDirection: true,
    });
  }

  _renderInfoBubble = ({message, className, icon, pos, tooltipSize, rightTooltipDirection=false}) => {
    // factory function for creating icons that show info bubbles when hovered over.
    // alt is only used when isSvg is false
    return (
      <div className={`InfoBubble-icon-container ${rightTooltipDirection?'right-tooltip':''} ${className}`}
      style={Object.assign({}, ...pos.split(' ').map(k=>({[k]:0})) )}>
        <div className='InfoBubble-icon'>{icon}</div>
        <div className='InfoBubble-tooltip' style={{width:tooltipSize[0], height:tooltipSize[1]}}>{message}</div>
        <div className='InfoBubble-tooltip-triangle'/>
      </div>
    );
  }
}

export default Util;
