import React from 'react';
import './BackupTile.css';
import backupIcon from './backup-vault.png'

class BackupTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-backup'/>
  }
  render_data() {
    let date = this.props.data.toLocaleString('en-us', {month: 'short', day: 'numeric', timeZone: 'UTC'})

    return (
      <a className='widget widget-backup' target='_blank' rel='noopener noreferrer'
      href='/wp-admin/admin.php?page=pb_backupbuddy_backup'>
        <img className='widget-backup-icon' src={backupIcon} alt='' draggable='false'></img>
        <h2 className='widget-backup-value'>Last<br/>Backup<div>{date}</div></h2>
      </a>
    );
  }
  render_error() {
    return (<div className='widget widget-backup'><span className='widget-backup-error'>Could not load the backups :(</span></div>)
  }
}

export default BackupTile;
