import React from 'react';
import './UpdateTile.css';
import updateIcon from './update-icon.png'

class UpdateTile extends React.Component {

  render() {
    if (!this.props.loaded) {
      return this.render_loading();
    } else if (this.props.loadFailed) {
      return this.render_error();
    } else {
      return this.render_data();
    }
  }
  render_loading() {
    return <div className='widget widget-update'/>
  }
  render_data() {
    let lastDateAvailable = this.props.lastDateAvailable;
    let OutOfDateRangeEl = this.props.util.OutOfDateRangeEl;

    return (
      <div className='widget widget-update'>
        <img className='widget-update-icon' src={updateIcon} alt='' draggable='false'></img>
        <div className='widget-update-text'>
          <h1 className='widget-update-value'>{this.props.data}</h1>
          <h2 className='widget-update-label'>Items<br/>Updated</h2>
        </div>
        {lastDateAvailable && (<OutOfDateRangeEl className='widget-update-info-icon' lastDay={lastDateAvailable}/>)}
      </div>
    );
  }
  render_error() {
    return (<div className='widget widget-update'><span className='widget-update-error'>Could not load the updates :(</span></div>)
  }
}

export default UpdateTile;
