// Higher order compontent in charge of fetching data.
import React from 'react';
import BackupTile from './BackupTile';

class Backup extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    data: undefined,
  };

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady() {
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
    });
    this.props.util.reportingPost('/report/get', {
      location: 'backups',
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }
  onDataReady(data) {
    let last = null
    for (let row of JSON.parse(data)) {
      let timestamp = parseInt(row.timestamp,10)
      if (timestamp > last) {
        last = timestamp;
      }
    }
    this.setState({
      loaded: true,
      data: new Date(last),
    });
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    })
  }

  render() {
    return <BackupTile data={this.state.data} loaded={this.state.loaded} loadFailed={this.state.loadFailed}/>
  }
}

export default Backup;
