import React from 'react';
import './Footer.css';

class Footer extends React.Component {
  footerEl = null;
  isColorAltered = false;
  removeScrollListener = null

  componentDidMount() {
    this.removeScrollListener = this.props.addScrollListener((...args) => this.onScroll(...args));
  }
  componentWillUnmount() {
    this.removeScrollListener();
  }

  render() {
    let footerStyle = {opacity:this.props.hide?0:1};
    let footerRef = (footer) => this.footerEl = footer;

    return (
      <div className='footer' style={footerStyle} ref={footerRef}>
        <h1 className='footer-maintext'>100% OVERWATCHED!</h1>
        <h1 className='footer-icon' aria-hidden/>
      </div>
    );
  }

  onScroll(utilities) {
    let distance = this.footerEl.getBoundingClientRect().top
    if (distance > 500) {
      if (this.isColorAltered) {
        utilities.setBackgroundColor('rgba(0,80,0,0)');
        this.isColorAltered = false;
      }
      return;
    } else {
      this.isColorAltered = true;
      let opacity = ((500-distance)/500);
      if (opacity > 0.99) opacity = 0.99; // Work around for bug on chrome where if you scroll all the way down, then scroll up one tick the footer dissapears. As long as the opacity never reaches 1, this doesn't happen.
      utilities.setBackgroundColor(`rgba(0,80,0,${opacity})`);
    }
  }
}

export default Footer;
