// Higher order compontent in charge of fetching data.
import React from 'react';
import AnalyticsTile from './AnalyticsTile.js';

class Analytics extends React.Component {

  state = {
    loaded: false,
    loadFailed: false,
    authed: null,
    data: undefined,
    dataXMap: undefined,
  };

  componentDidMount() {
    this.props.util.onRequestReady((...a) => this.onRequestReady(...a));
  }
  onRequestReady(days) {
    this.setState({
      loaded: false,
      loadFailed: false,
      data: undefined,
      dataXMap: undefined,
    });
    this.props.util.reportingPost('/analytic/sessions', {
      days: days,
      target: this.props.util.dashboardTarget,
    }, {method:'GET'})
    .onData((d) => this.onDataReady(d))
    .onError(() => this.onDataError())
    .send();
  }
  onDataReady(data) {
    if (data === 'NoViewId') {
      this.setState({
        loaded: true,
        authed: false,
      });
    } else {
      let values = JSON.parse(data);
      let dataXMap = {};
      for (let i=0; i<values.length; ++i) {
        dataXMap[i] = values[i][0];
        values[i][0] = i;
      }
      this.setState({
        loaded: true,
        authed: true,
        data: [{
          "key" : "Sessions" ,
          "color": "#a00",
          "values" : values,
        }],
        dataXMap,
      });
    }
  }
  onDataError() {
    this.setState({
      loaded: true,
      loadFailed: true,
    })
  }

  render() {
    return (
      <AnalyticsTile data={this.state.data} loaded={this.state.loaded}
                     dataXMap={this.state.dataXMap}
                     authed={this.state.authed} loadFailed={this.state.loadFailed}
                     changePage={this.props.changePage} util={this.props.util}/>
    );
  }
}

export default Analytics;
